import React from "react";
import styled from "styled-components";
import { InnerLayout } from "../styles/Layouts";
import Card from "./Card";
import card from "../img/creditcard.svg";
import active from "../img/active.svg";
import inactive from "../img/inactive.svg";
import check from "../img/check.svg";
import checkdisabled from "../img/check-disabled.svg";
function PaymentSection() {
  return (
    <PaymentSectionStyled>
      <InnerLayout>
        <h2 className="small-heading">
          An exceptional service, <span>at the right price</span>
        </h2>
        <p className="c-para">
          Start with our free plan and switch to premium as you grow.
        </p>
        <div className="card-con">
          <Card
            account={"Free"}
            amount={"$0"}
            text={"Manage your bussiness with a simple and efficient account."}
            button={"Get Started."}
            card={card}
            active={active}
            inactive={inactive}
            check={check}
            checkdisable={checkdisabled}
            text1={"10 free local transfers"}
            text2={"Free ATM withdrawals in Dollar up to $250 per month"}
            text3={"Free payments to other Draft account"}
            text4={"Prepaid debit cards"}
            text5={"Virtual card"}
            text6={"Priority 24/7 support"}
            text7={"Exchange 24 currencies"}
            text8={"Multi-user access"}
          />
          <Card
            account={"Free"}
            amount={"$0"}
            text={"Manage your bussiness with a simple and efficient account."}
            button={"Get Started."}
            card={card}
            active={active}
            inactive={inactive}
            check={check}
            checkdisable={check}
            text1={"10 free local transfers"}
            text2={"Free ATM withdrawals in Dollar up to $250 per month"}
            text3={"Free payments to other Draft account"}
            text4={"Prepaid debit cards"}
            text5={"Virtual card"}
            text6={"Priority 24/7 support"}
            text7={"Exchange 24 currencies"}
            text8={"Multi-user access"}
          />
        </div>
      </InnerLayout>
    </PaymentSectionStyled>
  );
}

const PaymentSectionStyled = styled.section`
  .card-con {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
    /* width: 90%; */
    margin: 0 auto;
    padding-top: 3rem;
    @media screen and (max-width: 689px) {
      grid-template-columns: repeat(1, 1fr);
    }
    .c-para {
      text-align: center;
    }
  }
`;

export default PaymentSection;
